import { schema } from 'normalizr'

export const DiarioItemSchema = new schema.Entity('diario-item', {}, { idAttribute: '_id' })

export const DiarioRegistroSchema = new schema.Entity('diario-registro', {
    itens: [DiarioItemSchema],
}, { idAttribute: '_id' })






