import React from 'react'

import { Menu, Segment, Icon, Button, Image, Item, Popup, Label, Grid, Header } from 'semantic-ui-react'
import { Link, NavLink } from 'react-router-dom'

import DialogItem from './../../../containers/dialog-item/container'

const LabelSituacao = ({ situacao }) => {
    if (situacao === 'finalizada') {
        return <Label style={{ float: 'right' }} color='green' content='Finalizado' />
    } else if (situacao === 'pendente') {
        return <Label style={{ float: 'right' }} color='orange' content='Pendente' />
    } else {
        return <Label style={{ float: 'right' }} content='Informativo' />
    }
}

//import SemImagemDiario from './../../../../../components/image-tiler/sem-imagem.jpg'
/* Style imagem
    padding: '5px', 
    'max-height': '180px',
    'max-width': '270px'
*/

export default ({
    itens, item, diario, projeto, onMigrar
}) => (
        <div>
            <Menu borderless attached="top" >
                <Menu.Item header content='Itens' />
                <Menu.Menu position='right'>
                    <DialogItem diarioId={diario._id}>
                        <Menu.Item icon="plus" content="Adicionar" />
                    </DialogItem>
                </Menu.Menu>
            </Menu>

            <Segment attached>

                <Item.Group divided>

                    {(itens || []).map((d) => (
                        <Item key={d._id}>
                            <Item.Content>
                                <Grid>
                                    <Grid.Row>
                                        <Grid.Column width={14}>
                                            <Header size='tiny'>{d.assunto}</Header>
                                        </Grid.Column>
                                        <Grid.Column width={2}>
                                            <LabelSituacao situacao={d.situacao} />
                                        </Grid.Column>
                                    </Grid.Row>
                                </Grid>
                                <Item.Meta>
                                    <span className='cinema'>Responsavel: {d.responsavel}</span>
                                </Item.Meta>
                                <Item.Description>
                                    <p style={{ whiteSpace: 'pre-line' }}>{d.descricao}</p>
                                    <Image.Group>
                                        {d.imagens.map(i => <Image src={i.url} size="small" />)}
                                    </Image.Group>
                                </Item.Description>
                                <Item.Extra>
                                    <DialogItem id={d._id}>
                                        <Button basic icon='edit' content='Editar' floated='right' />
                                    </DialogItem>
                                    <Button basic icon='camera' content='Inserir imagem' floated='right' />
                                </Item.Extra>
                            </Item.Content>
                        </Item>
                    ))}

                </Item.Group>

            </Segment>

        </div>
    )


