import React from 'react'

import { Grid, Menu, Popup } from 'semantic-ui-react'

import Loader from './../../../../../components/loader'
import Registro from './registro'
import Item from './item'

import DialogDiario from './../../../containers/dialog-diario/cadastro.container'
import DialogExcluirDiario from './../../../containers/dialog-diario/excluir.container'

export default (props) => {

    const {
                carregando, onMigrar
            } = props

    return (
        <Loader loading={carregando}>
            <Grid container>
                <Grid.Row>
                    <Grid.Column>
                        <Menu>
                            <Menu.Item>{props.diario.local}</Menu.Item>

                            <DialogDiario id={props.diario._id}>
                                <Menu.Item icon="edit" content="Editar" />
                            </DialogDiario>

                            <Popup
                                key="revisao"
                                position="bottom center"
                                content="Criar uma nova revisão do diário de obra atual, mantendo apenas os assuntos pendentes e informativos."
                                trigger={
                                    <Menu.Item
                                        icon="browser"
                                        content="Nova revisão"
                                        onClick={() => { onMigrar(props.diario._id) }} />
                                } />

                            <Menu.Menu position='right'>
                                <DialogExcluirDiario id={props.diario._id} projetoId={props.projeto}>
                                    <Menu.Item icon="trash" content="Excluir" />
                                </DialogExcluirDiario>
                            </Menu.Menu>
                        </Menu>
                    </Grid.Column>
                </Grid.Row>
                <Grid.Row>
                    <Grid.Column width={4}>
                        <Registro
                            diario={props.diario}
                        />
                    </Grid.Column>
                    <Grid.Column width={12}>
                        <Item
                            itens={props.itens}
                            item={props.item}
                            diario={props.diario}
                            projeto={props.projeto}
                            onMigrar={onMigrar}
                        />
                    </Grid.Column>
                </Grid.Row>
            </Grid>
        </Loader>
    )
}    


